import echarts from 'echarts';

require('echarts/theme/macarons'); // echarts theme


import { debounce } from '@/utils';
var animationDuration = 1000;
export default {
  props: {
    className: {
      type: String,
      default: 'chart'
    },
    width: {
      type: String,
      default: '100%'
    },
    height: {
      type: String,
      default: '500px'
    },
    chartData: {
      type: Object,
      required: true
    }
  },
  data: function data() {
    return {
      chart: null
    };
  },
  watch: {
    chartData: {
      deep: true,
      handler: function handler(val) {
        this.initChart(val);
      }
    }
  },
  mounted: function mounted() {
    var _this = this;

    this.initChart();
    this.__resizeHandler = debounce(function () {
      if (_this.chart) {
        _this.chart.resize();
      }
    }, 100);
    window.addEventListener('resize', this.__resizeHandler);
  },
  beforeDestroy: function beforeDestroy() {
    if (!this.chart) {
      return;
    }

    window.removeEventListener('resize', this.__resizeHandler);
    this.chart.dispose();
    this.chart = null;
  },
  methods: {
    initChart: function initChart() {
      this.chart = echarts.init(this.$el, 'macarons');
      this.chart.setOption({
        tooltip: {
          trigger: 'axis',
          axisPointer: {
            // 坐标轴指示器，坐标轴触发有效
            type: 'shadow' // 默认为直线，可选为：'line' | 'shadow'

          }
        },
        toolbox: {
          show: true,
          feature: {
            saveAsImage: {
              show: true
            }
          }
        },
        calculable: true,
        grid: {
          top: 10,
          left: '10%',
          right: '2%',
          bottom: '3%',
          containLabel: true
        },
        xAxis: [{
          type: 'value',
          axisLine: {
            show: false
          },
          axisTick: {
            show: false
          }
        }],
        yAxis: [{
          type: 'category',
          data: this.chartData.yType,
          splitLine: {
            show: false
          },
          axisLine: {
            show: false
          },
          axisTick: {
            show: false
          },
          offset: 10,
          nameTextStyle: {
            fontSize: 15
          }
        }],
        series: [{
          name: '新客户',
          type: 'bar',
          stack: '总数',
          data: this.chartData.newData,
          animationDuration: animationDuration,
          color: '#3AA1FF' // itemStyle: {
          //   normal: {
          //     label: {
          //       show: true, // 开启显示
          //       position: 'insideRight',
          //       color: '#fff'
          //     }
          //   }
          // }

        }, {
          name: '老客户',
          type: 'bar',
          stack: '总数',
          data: this.chartData.oldData,
          animationDuration: animationDuration,
          color: '#4ECB73' // itemStyle: {
          //   normal: {
          //     label: {
          //       show: true, // 开启显示
          //       position: 'insideRight',
          //       color: '#fff'
          //     }
          //   }
          // }

        }]
      });
    }
  }
};